import React, {createContext, useContext, useEffect, useState} from 'react'

interface ContextType {
  user: IAuthUserState | null;
  login: () => void | null;
  logout: () => void | null;
}
type useProviderAuthReturnType = ContextType;

const authContext = createContext<ContextType | null>(null);

export const AuthProvider: React.FC<{}> = (props) => {
  const { children } = props;
  const auth = useProviderAuth();
  return <authContext.Provider value={auth}>{children}</authContext.Provider>;
}

export const useAuth = () => {
  const auth = useContext(authContext);
  if(!auth) throw new Error('Cannot auth!!');
  return auth;
}

type IAuthUserState = {
  auth: boolean;
  userName: string | null;
  userToken: string | null;
}




function useProviderAuth() : useProviderAuthReturnType {
  const [ user, setUser ] = useState<IAuthUserState | null>(null);

  // TODO    auth  .
  const login = () => {
    setUser({
      auth: true,
      userName: 'bobin',
      userToken: '23423q5235aff234r23232rf23rf323r23423423f232r'
    })
  }

  const logout = () => {
    setUser(null);
  }

  return {
    user,
    login,
    logout,
  }
}

